html {
  height: 100%;
  min-height: 100%;
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Source Sans Pro", sans-serif;
}

body {
  min-height: 100%;
  font-family: "Source Sans Pro", sans-serif;

  &.vr-homepage {
    &::before {
      background-image: -webkit-linear-gradient(149deg, #c45b6c, #fc7f45);
      background-image: -o-linear-gradient(149deg, #c45b6c, #fc7f45);
      background-image: linear-gradient(239deg, #c45b6c, #fc7f45);
      height: 75vh;
      top: 0px;
      position: absolute;
      bottom: 0;
      display: block;
      content: "";
      width: 100vw;
      z-index: 1;
      filter: alpha(opacity=62);
      /* internet explorer */
      -khtml-opacity: 0.62;
      /* khtml, old safari */
      -moz-opacity: 0.62;
      /* mozilla, netscape */
      opacity: 0.62;
      /* fx, safari, opera */

    }

    &::after {
      background: white;
      content: "";
      height: 25vh;
      width: 100vw;
      position: absolute;
      bottom: 0;
      display: block;

    }
  }

}

/* calendar widget */
.widget_calendar {
  float: left;
}

#wp-calendar {
  width: 100%;
}

#wp-calendar caption {
  text-align: right;
  color: #333;
  font-size: 12px;
  margin-top: 10px;
  margin-bottom: 15px;
}

#wp-calendar thead {
  font-size: 10px;
}

#wp-calendar thead th {
  padding-bottom: 10px;
}

#wp-calendar tbody {
  color: #aaa;
}

#wp-calendar tbody td {
  background: #f5f5f5;
  border: 1px solid #fff;
  text-align: center;
  padding: 8px;
}

#wp-calendar tbody td:hover {
  background: #fff;
}

#wp-calendar tbody .pad {
  background: none;
}

#wp-calendar tfoot #next {
  font-size: 10px;
  text-transform: uppercase;
  text-align: right;
}

#wp-calendar tfoot #prev {
  font-size: 10px;
  text-transform: uppercase;
  padding-top: 10px;
}

img.alignright {
  float: right;
  margin: 0 0 1em 1em;
}

img.alignleft {
  float: left;
  margin: 0 1em 1em 0;
}

img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.alignright {
  float: right;
}

.alignleft {
  float: left;
}

.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

